<style scoped>
.tags{
    padding:20px 10px;
}
.tags span{
    background-color: #777;
    border-radius: 3px;
    color:#FFF;
    padding: 2px 5px;
    font-size: 14px;
    margin-right: 5px;
    cursor: pointer;
}
.tag_title{
    background-color: #efefef;
    padding: 10px;
    border-radius: 5px;
    font-size: 20px;
    font-weight: 800;
}
.list>div{
    overflow: hidden;
    width:90%;
    padding: 10px 0;
    border:1px solid #ddd;
    border-top: none;
    margin:0 5%;
    border-radius: 5px;
    cursor: pointer;
}
.list>div>span{
    /*display: inline;*/
    width:auto;
    margin: 0 10px;
}
</style>

<template :is="view"
               transition="fade"
               transition-mode="out-in">

    <div class="row">
        <div class="col-lg-8 col-lg-offset-1 col-md-8 col-md-offset-1 col-sm-12 col-xs-12 postlist-container">
            <div class="tags">
                <span v-for='tag in tags' @click="jumpTo(tag)">{{tag}}</span>
            </div>
            <div class="" v-for="(tag, key) in tags">
                <div class="tag_title" :id="tag">{{tag}}</div>
                <div v-for="list in lists" v-if="list.tag == tag" class="list">
                    <router-link :to="{path: list.path}" tag="div">
                        <span class="pull-left">{{list.title}}</span>
                        <span class="pull-right">{{list.date}}</span>
                    </router-link>
                </div>
            </div>
        </div>
        <div class="col-lg-3 col-lg-offset-0 col-md-3 col-md-offset-0 sidebar-container visible-lg-block visible-md-block">
            <about></about>
        </div>
    </div>

</template>

<script>
import summaryList from '../components/summary.vue'
import about from '../components/about.vue'
export default {
    components: {
        summaryList,
        about
    },
    data() {
        return {
            lists: this.$store.state.articles,
            tags: this.$store.state.tags
        }
    },
    mounted() {
        if (this.$route.query.tagName) {
            this.jumpTo(this.$route.query.tagName)
        }
    },
    methods: {
        jumpTo(id) {
            document.getElementById(id).scrollIntoView();
        }
    }
}

</script>



